//========================Начало глобальных стилей===========================/
.container {
   max-width: 1920px;
   margin: 0 auto;
}

body {
   padding: 0;
   margin: 0 auto;
   transition: all 0.3s;
}

* {
   box-sizing: border-box;
}

ul,
li,
a {
   text-decoration: none;
   margin: 0;
   padding: 0;
   list-style: none;
   display: inline-block;
}

input,
textarea {
   font-size: 13px;
   font-family: 'OpenSans-Bold';
   color: #413f53;
}

body {
   font-size: 15px;
   color: black;
   font-family: 'OpenSans-Regular';
}

.logo img {
   width: auto;
   height: 60px;
}

//========================Подключение шрифтов===========================/
@font-face {
   font-family: 'OpenSans-Bold';
   src: url(../assets/fonts/OpenSans-Bold.woff) format('woff');
}

@font-face {
   font-family: 'OpenSans-Regular';
   src: url(../assets/fonts/OpenSans-Regular.woff) format('woff');
}

//========================Стиль кнопки===========================/
.btn {
   color: #fff;
   background-color: #8682b6;
   border-top-right-radius: 50px;
   border-bottom-right-radius: 50px;
   border-top-left-radius: 100px;
   text-transform: uppercase;
   font-family: 'OpenSans-Bold';
   width: 290px;
   outline: none;
   transition: all 0.3s;
   border: 2px solid #8682b6;
   cursor: pointer;
   height: 68px;
   display: flex;
   justify-content: center;
   align-items: center;
   margin: 0 auto;

   &:hover {
      border: 2px solid #63608b;
   }
}

:focus::-webkit-input-placeholder {
   color: transparent;
}

//========================Стиль кнопки плюс===========================/
.buttonPlus {
   width: 37px;
   height: 37px;
   position: absolute;
   border: 3px solid #bab4ff;
   background-color: #8682b6;
   border-radius: 50%;
   cursor: pointer;
   outline: none;
   top: 50%;
   transform: translateY(-50%);
   right: -60px;
   transition: all 0.4s;

   &:hover {
      border: 3px solid #69668f;
   }

   &-item {
      position: absolute;
      top: 48%;
      left: 9px;
      transform: translate(-50%, -50%);
      height: 3px;
      width: 13px;
      background-color: #fff;
      transform: rotate(90deg);

      &:nth-child(2) {
         transform: rotate(-180deg);
      }
   }
}

//========================Выпадающий список===========================/
.list-select {
   display: flex;
   align-items: center;
   margin: 0 20px;
   .select {
      display: flex;
      justify-content: center;
   }

   .list-select-text {
      margin-right: 20px;
      font-family: 'OpenSans-Bold';
   }

   .list-select-selector {
      background-color: rgb(237, 237, 237);
      width: 140px;
      font-family: 'OpenSans-Bold';
      border: 0;
      border-top-left-radius: 50px;
      border-bottom-left-radius: 50px;
      border-top-right-radius: 50px;
      border-bottom-right-radius: 50px;
      outline: none;
      padding: 5px 15px;
      color: #413f53;
      -webkit-appearance: none;
      height: 28px;
      display: flex;
      justify-content: left;
      align-items: center;
      position: relative;
      z-index: 50;
      border: 1px solid #fff;
      transition: all 0.4s;
   }

   .select.active .list-select-selector {
      border: 1px solid rgb(237, 237, 237);
      background-color: #fff !important;
   }

   .active {
      &:before {
         top: -5px !important;
         transform: rotate(180deg);
      }
   }

   .select.active .select__body,
   .select.active .select__body-bg {
      display: block;
   }

   .select__body-bg {
      display: none;
      position: absolute;
      left: 50%;
      top: 60%;
      transform: translateX(-50%);
      background-color: rgb(237, 237, 237);
      border: 1px solid #ccc;
      height: 12px;
      width: 98%;
      border-bottom: 0;
   }

   .select__body {
      display: none;
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      top: 100%;
      width: 98%;
      z-index: 30;
      text-align: left;
      max-height: 355px;
      overflow: auto;
      padding-left: 15px;
      padding: 5px 0px 5px 15px;
      background-color: rgb(237, 237, 237);
      border: 1px solid #ccc;
      border-top: 0;

      &::-webkit-scrollbar {
         width: 18px !important;
      }

      &::-webkit-scrollbar-track {
         padding: 0 11px 0 5px;
         background-color: rgb(237, 237, 237);
      }

      &::-webkit-scrollbar-thumb {
         background-color: #c9c9c9;
         padding: 0 11px 0 5px;
         border-top: 3px solid #ededed;
         border-bottom: 3px solid #ededed;
         border-right: 5px solid #ededed;
         border-left: 5px solid #ededed;
         border-radius: 8px;
      }
   }

   &-option {
      cursor: pointer;
      font-family: 'OpenSans-Bold';
      font-size: 13px;
      color: #413f53;
      padding-left: 4px;
      margin-left: -4px;
      width: 106px;

      &:hover {
         background-color: #ccc;
      }
   }

   .select__current {
      font-size: 13px;
   }

   .select {
      position: relative;
      cursor: pointer;

      &:before {
         content: '▼';
         padding: 0 8px;
         font-size: 12px;
         position: absolute;
         right: 8px;
         top: 6px;
         z-index: 55;
         text-align: center;
         width: 10%;
         height: 100%;
         pointer-events: none;
      }
   }
}

@media (max-width: 1000px) {
   .list-select {
      margin: 10px 20px;
      position: relative;
   }
   .list-select-selector {
      position: relative;
      z-index: 5;
   }
   .list-select .select:before {
      z-index: 20;
   }
   .select__body {
      position: relative;
      z-index: 999;
   }
   .list-select-text {
      width: 60px;
   }
}

//=======================Чекбокс===========================/
.checkbox {
   display: none;
}
.fake {
   display: inline-block;
   width: 16px;
   height: 16px;
   border: 1px solid #413f53;
   position: relative;
   &::before {
      content: '';
      position: absolute;
      width: 10px;
      height: 10px;
      background-color: #00cea3;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      opacity: 0;
      transition: all 0.4s;
   }
}

.checkbox:checked + .fake::before {
   opacity: 1;
}

//=======================Таблица===========================/
.table__block-wrapper {
   overflow-x: hidden;
   background-color: #fff;
}

.table__block-table {
   width: 100%;
   border-collapse: collapse;
   height: 100%;
   .table__block-title {
      max-height: 35px;

      th {
         padding: 10px 0;
         margin: 0;
         border: 0px solid #8682b6;
         position: sticky;
         top: 0;
         z-index: 10;
         background-color: #8682b6;
      }

      color: #fff;
      background-color: #8682b6;
   }

   td {
      border: 1px solid #ededed;
   }

   .table__block-table-text {
      background-color: #d7e4e6;
      &:hover {
         background-color: #a5dbe3;
      }
   }

   td:first-child {
      span {
         display: inline-block;
         position: relative;

         &:before {
            content: '';
            width: 17px;
            height: 17px;
            background-repeat: no-repeat;
            position: absolute;
            left: -20px;
            top: 50%;
            transform: translateY(-50%);
         }
      }
   }
}

@media (max-width: 1100px) {
   .table__block-wrapper {
      overflow-x: auto;
   }
}

@media (max-width: 1000px) {
   table {
      font-size: 10px;
   }
   table span {
      padding-left: 10px;
   }
   .table__block-table td:first-child span:before {
      left: -8px;
   }
   .table_block-top-subtitle {
      margin: 0;
   }
}

//===Скроллбар таблицы===/
::-webkit-scrollbar {
   width: 22px;
}

::-webkit-scrollbar-track {
   width: 22px;
   background-color: #ededed;
}

::-webkit-scrollbar-thumb {
   background-color: #c9c9c9;
   border-top: 3px solid #ededed;
   border-bottom: 3px solid #ededed;
   border-right: 5px solid #ededed;
   border-left: 5px solid #ededed;
   border-radius: 10px;
}
